import {
    Observable
} from 'rxjs';

import {
    Injectable
} from '@angular/core';

import {
    HttpClient,
    HttpParams
} from '@angular/common/http';

import {
    IListData
} from '../models';

import {
    environment
} from '../../environments/environment';

import { MiscUtilities } from '../utlities';

@Injectable()
export class RequestService {

    constructor(
        private _httpClient: HttpClient) {

    }

    requestAnOperation(fid: string, serverName: string, requestor: string, action: string, team: string,
        paramNames: IListData[], paramValues: string[]): Observable<any> {

        const url = environment.coatApiUrl + 'RequestAnOperation';
        let params = new HttpParams();
        params = params
            .append('fid', fid)
            .append('server', serverName)
            .append('requestor', requestor)
            .append('action', action)
            .append('team', team);

        let idx = 0;
        paramNames.forEach(n => {
            params = params.append(n.value, paramValues[idx++]);
        });

        const headers = MiscUtilities.createHeaders();
        return this._httpClient
            .get<boolean>(url, { headers: headers, params: params });
    }

}


