
import {
    Observable
} from 'rxjs';

import {
    Injectable
} from '@angular/core';

import {
    HttpClient,
    HttpParams
} from '@angular/common/http';

import {
    IListData
} from '../models';

import {
    environment
} from '../../environments/environment';

import { MiscUtilities } from '../utlities';

@Injectable()
export class ListDataService {

    constructor(
        private _httpClient: HttpClient) {

    }

    getCommandList(): Observable<IListData[]> {

        const url = environment.coatApiUrl + 'commandList';
        const params = new HttpParams().set('name', 'none');
        const headers = MiscUtilities.createHeaders();

        return this._httpClient
            .get<IListData[]>(url, { headers: headers, params: params });
    }

    getFileCommandList(requestor: string, action: string): Observable<IListData[]> {

        const url = environment.coatApiUrl + 'form';
        const params = new HttpParams()
            .set('action', action) // 'SCP File from your server to destination')
            .set('requestor', requestor)
            .set('team', 'Big');
        const headers = MiscUtilities.createHeaders();

        return this._httpClient
            .get<IListData[]>(url, { headers: headers, params: params });
    }

}


